import React from "react";
import "./ProjectIndicator.css";
import { useSelector } from "react-redux";

function ProjectIndicator() {
  const { activeProject } = useSelector((state) => state.taskpane);

  return (
    <div className="projectIndicator-box">
      <span>...</span> <span>{activeProject?.name}</span>
    </div>
  );
}

export default ProjectIndicator;
